.contact textarea {
 font-size: 16px;
 width: 80%;
 min-height: 200px;
 margin: 20px 0;
 line-height: 1.6;
}

.contact button{
    display: block;
    margin: 0 20% 0 auto;
    border: 2px solid black;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    transition: 0.4s;
}


.close:hover,
.close:focus {
    color: white;
    box-shadow: 
        inset -3.5em 0 0 0 green,
        inset 3.5em 0 0 0 green;  
}